export const post = (url, data) => {
    // var defer = $.Deferred();
    return new Promise((resolve, reject)=>{
        $.ajax({
            url: url,
            data: JSON.stringify(data),
            method: 'post',
            dataType: 'json',
            contentType: 'application/json',
            processData: false
        })
        .done(function (res) {
            if (res.code) {
                reject(res);
            } else {
                resolve(res);
            }
        })
        .fail(function (res) {
            reject(res);
        });
    })
}