<template>
<div>
    <header></header>
    <section class="content">

        <h1  style="text-align:center;"></h1>
        <h2  style="text-align:center;"></h2>
        <div class="account-wrap">
            <div ref="tip" class="tip"><strong>X</strong>&nbsp;<span ref="notice"></span></div>
            <form ref="loginWrap" class="login" onsubmit="return false;">
                <el-input placeholder="" v-model="username"  name="username"/>
                <el-input placeholder="" v-model="password"  type="password" name="password"/>

                <div class="forgot">
                    <label>
                        <a href="javascript:;" @click="forgetPassword()"></a>
                    </label>
                </div>
                <el-button native-type="submit"  type="primary" id="login" @click="login($event)"></el-button>
            </form>

            <form ref="modifyWrap" class="modify-password" >
                <div class="form-group">
                    <input type="text" v-model="mobile" @focus="hideTip" class="form-control" placeholder="" maxlength="11">
                </div>

                <div class="form-group">
                    <!--count-down ref="countDown" :auto-start="false" :time="59*1000">
                        <template slot-scope="count">
                            <button type="button" :disabled="count.counting" class="btn btn-default btn-block unclick" @click="getVerifyCode($event)">{{count.counting?count.seconds+'S':''}}</button>
                        </template>
                    </count-down -->
                </div>
                <div class="return-login-wrap">
                    <button type="button" class="btn btn-default btn-block" @click="loginNow"></button>
                </div>
                <div class="reset-wrap">
                    <div class="form-group">
                        <input type="text" v-model="verifyCode" class="form-control" name="code" placeholder="4" maxlength="4">
                    </div>
                    <div class="form-group">
                        <input v-model="newPassword" name="newpassword" class="form-control" placeholder="6-16" maxlength="16">
                    </div>
                    <button @click="resetPassword($event)" type="submit" class="btn btn-primary btn-block" id="reset"></button>
                </div>
            </form>
        </div>
    </section>

</div>
</template>
<style lang="scss" scoped>
    .forgot{
        display: none;
    }
    .modify-password{
        display: none;
    }
    .login{
        height: 200px
    }
    .account-wrap {
        padding: 2rem 2.5rem;
        width: 500px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
        .login{
            display: flex;
            align-items: center;
            justify-content: space-around;
            flex-direction: column;
        }
        > .tip {
            display: none;
            padding: .2rem;
            font-size: .75rem;
            color: red;
        }
        .option {
            // @include flex-container();
            // justify-content: space-between;
            // align-items: baseline;
            padding: .3rem 0;
            margin-bottom: .7rem;
            font-size: .7rem;
            text-align: right;
            a {
                color: #999;
            }
        }

        hr {
            margin: 1rem 0 2rem;
        }
    }
</style>
<script>
import md5 from "md5";
import { post }  from '../../common/function/http';
//import CountDown from "common/components/count-down.js";
export default {
  components: {
    //CountDown
  },
  data() {
    return {
      formstate: {},
      mobile: null,
      verifyCode: "",
      newPassword: "",
      username: "",
      password: "",
    };
  },
  methods: {
    showTip(notice) {
      $(this.$refs.notice).text(notice);
      $(this.$refs.tip).show();
    },
    hideTip() {
      $(this.$refs.tip).hide();
    },
    forgetPassword() {
      this.hideTip();
      $(this.$refs.loginWrap)
        .trigger("reset")
        .hide();
      $(this.$refs.modifyWrap).fadeIn();
    },
    loginNow() {
      this.hideTip();
      $(this.$refs.modifyWrap)
        .trigger("reset")
        .hide();
      $(this.$refs.loginWrap).fadeIn();
    },
    startCountDown() {
      this.$refs.countDown.start();
      $("#code, #new-pass").val("");
      $(".return-login-wrap > button").hide();
      $(".reset-wrap").slideDown();
    },
    getVerifyCode(e) {
      if (this.checkMobile(this.mobile)) {
        post("/user/verifactionCode.json", {
          mobile: this.mobile,
          t: md5("lo&" + this.mobile + "ed!")
        })
          .then(() => {
            this.startCountDown();
          })
          .catch(this.handleError.bind(this));
      } else {
        this.showTip("");
      }
    },
    handleError(response) {
      const code = response.code;
      let msg;
      if (code && code > 0) {
        msg = response.msg || '';
      } else {
        msg = '';
      }
      this.showTip(msg);
    },
    checkMobile(s) {
      return /^1\d{10}$/.test(s);
    },
    checkPasword(s) {
      return /^.{6,16}$/.test(s);
    },
    checkCode(s) {
      return /^.{4}$/.test(s);
    },
    login(e) {
      e.preventDefault();
    if (this.checkPasword(this.password)) {
        post('/finance/auth/login', {
            username: this.username,
            password: this.password
        })
        .then(res=> {
            window.localStorage.setItem("token",res.data)
            window.location.href = '/main.html#/selection'
        })
        .catch(this.handleError.bind(this));
    } else {
        this.showTip("616");
    }

    },
    resetPassword(e) {
      e.preventDefault();
      if (this.checkMobile(this.mobile)) {
        if (this.checkCode(this.verifyCode)) {
          if (this.checkPasword(this.newPassword)) {
            post("/user/changePassword.json", {
              mobile: this.mobile,
              password: md5(this.newPassword),
              code: this.verifyCode
            }).then(() => {
              this.$message.success("");
              setTimeout(() => {
                window.location = location.href;
              }, 2500);
            })
            .catch(this.handleError.bind(this));
          } else {
            this.showTip("616");
          }
        } else {
          this.showTip("");
        }
      } else {
        this.showTip("");
      }
    }
  }
};
</script>
